import './arrowBackNavigation.css';
import IconButton from '@material-ui/core/IconButton';
import {ArrowBackIos} from '@material-ui/icons';
import {useHistory, NavLink} from 'react-router-dom';

function ArrowBackNavigation(props) {
  let history = useHistory();
  return (
    <div className="button-navigation-container">
      <NavLink className="back-screen" to={props.to}>
        {props.backScreen}
      </NavLink>
      <IconButton onClick={() => history.goBack()}>
        <ArrowBackIos fontSize="small" />
      </IconButton>
      <span className="current-screen">{props.currentScreen}</span>
    </div>
  );
}

export default ArrowBackNavigation;
