import {makeStyles} from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Checkbox from '../checkBox/checkBox';
import React from 'react';

const useStyles = makeStyles({
  table: {
    minWidth: 500,
    height: 600,
  },
});

function createData(resourceName, members, fullAccess, edit, view) {
  return {
    resourceName,
    members,
    fullAccess,
    edit,
    view,
  };
}

const rows = [
  createData('Roles & Permissions', 'Mark', true, false, true),
  createData('Records', 'Mark', false, true, false),
  createData('Roles & Permissions', 'Mark', true, true, false),
  createData('Records', 'Mark', false, true, false),
  createData('Records', 'Mark', true, true, false),
  createData('Employee Permissions', 'Mark', false, true, false),
];
export default function ReviewDetailsTable() {
  const [checked, setChecked] = React.useState(true);

  const handleChange = (event) => {
    setChecked(event.target.checked);
  };
  const classes = useStyles();

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} size="small" aria-label="a dense table">
        <TableHead>
          <TableRow>
            <TableCell>Resource Name</TableCell>
            <TableCell align="center">Members</TableCell>
            <TableCell align="center">Full Access</TableCell>
            <TableCell align="center">Edit</TableCell>
            <TableCell align="center">View</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow key={row.id}>
              <TableCell component="th" scope="row">
                {row.resourceName}
              </TableCell>
              <TableCell align="center">{row.members}</TableCell>
              <TableCell align="center">
                {row.fullAccess ? (
                  <Checkbox checked={checked} />
                ) : (
                  <Checkbox onChange={handleChange} />
                )}
              </TableCell>
              <TableCell align="center">
                {row.edit ? (
                  <Checkbox checked={checked} />
                ) : (
                  <Checkbox onChange={handleChange} />
                )}
              </TableCell>
              <TableCell align="center">
                {row.view ? (
                  <Checkbox checked={checked} />
                ) : (
                  <Checkbox onChange={handleChange} />
                )}
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
