import React, {useState, useEffect} from 'react';
import './newReviewStepper.css';
import {makeStyles} from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import InputField from '../textField/textField';
import InputSearch from '../../components/inputSearch/inputSearch';
import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline';
import TreeResours from '../treeResources/treeResources';
import {useHistory} from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  stepIcon: {
    color: '#607799',
  },
}));

function getSteps() {
  return ['Review type', 'Resource', 'Owner', 'Frequency', 'Commit'];
}

export default function NewReviewStepper() {
  const [formValues, setFormValues] = useState({
    reviewName: '',
    typeReview: '',
    resource: '',
    ownerSugest: '',
    frequency: '',
  });
  useEffect(() => {
    console.log('Form value', formValues);
  }, [formValues]);

  function handleFormValueChange(event) {
    console.log(event.target.value);
    setFormValues({
      ...formValues,
      [event.target.name]: event.target.value,
    });
  }
  function setSearchValue(fieldName) {
    console.log('name', fieldName);
    setFormValues({
      ...formValues,
      resource: fieldName,
    });
  }
  function setOwnerValue(item) {
    console.log('name', item);
    setFormValues({
      ...formValues,
      ownerSugest: item,
    });
  }
  function getStepContent(stepIndex) {
    switch (stepIndex) {
      case 0:
        return (
          <div className="center-content">
            <InputField
              type="text"
              placeholder="  ..."
              label="Review Name:"
              name="reviewName"
              handleChange={handleFormValueChange}
              value={formValues.reviewName}
            />
            <FormControl component="fieldset" onChange={handleFormValueChange}>
              <p className="lengend-radio">Select a type of review</p>
              <RadioGroup
                aria-label=""
                name="typeReview"
                // onChange={handleFormValueChange}
              >
                <FormControlLabel
                  value="roleAssignments"
                  control={<Radio className="radio-button-stepper" />}
                  label="Role Assignments"
                />
                <FormControlLabel
                  value="groupMembership"
                  control={<Radio className="radio-button-stepper" />}
                  label="Group Membership"
                />
                <FormControlLabel
                  value="rolePermissions"
                  control={<Radio className="radio-button-stepper" />}
                  label="Role Permissions"
                />
                <FormControlLabel
                  value="employee-permission"
                  control={<Radio className="radio-button-stepper" />}
                  label="Employee Permission"
                />
                <FormControlLabel
                  value="sensitive-data"
                  control={<Radio className="radio-button-stepper" />}
                  label="Sensitive Data"
                />
              </RadioGroup>
            </FormControl>
          </div>
        );
      case 1:
        return (
          <div className="center-content mui-inner">
            <p className="text-stepper">Select a resource</p>
            <form>
              <InputField
                defaultValue=""
                name="resource"
                type="text"
                placeholder="Search"
                label="Select Resource:"
                onChange={handleFormValueChange}
                value={formValues.resource}
              />
              <TreeResours setValueItem={setSearchValue} />
            </form>
          </div>
        );
      case 2:
        return (
          <div className="center-content">
            <form>
              <InputSearch
                name="ownerSugest"
                onChange={handleFormValueChange}
                value={formValues.ownerSugest}
                setValueItem={setOwnerValue}
              />
            </form>
          </div>
        );
      case 3:
        return (
          <div className="center-content">
            <p className="text-stepper">Select the frequency of this review</p>
            <select
              name="frequency"
              className="select-stepper"
              onChange={handleFormValueChange}
            >
              <option value="Monthly">Monthly</option>
              <option value="Quarterly">Quarterly</option>
              <option value="Annually">Annually</option>
              <option value="BiAnnually">Bi-annually</option>
            </select>
          </div>
        );
      case 4:
        return (
          <div className="center-content">
            <p className="text-stepper">Review Name: {formValues.reviewName}</p>
            <p className="text-stepper">Review type: {formValues.typeReview}</p>
            <p className="text-stepper">Resource: {formValues.resource}</p>
            <p className="text-stepper">Owner: {formValues.ownerSugest}</p>
            <p className="text-stepper">Frequency: {formValues.frequency}</p>
            <p className="confirm-text">
              Are you sure you want to save this information? Press
              <span style={{color: '#607799'}}> Create Review</span>
            </p>
          </div>
        );
      default:
        return 'Unknown stepIndex';
    }
  }

  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const steps = getSteps();
  let history = useHistory();

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };
  const handleGoHomePage = () => {
    history.push('/strongpoint-reviews');
  };

  return (
    <div className="stepper-content">
      <h1 className="text-stepper">CREATE REVIEW</h1>
      <Stepper activeStep={activeStep} alternativeLabel>
        {steps.map((label) => (
          <Step key={label}>
            <StepLabel
              StepIconProps={{
                classes: {
                  root: classes.stepIcon,
                  active: classes.stepIconActive,
                  completed: classes.stepIcon,
                  text: classes.stepIconText,
                },
              }}
            >
              {label}
            </StepLabel>
          </Step>
        ))}
      </Stepper>
      <div style={{marginTop: 20}}>
        {activeStep === steps.length ? (
          <div className="success-icon-container">
            <CheckCircleOutlineIcon style={{fontSize: 200, color: '#31be3f'}} />
            <p className="instructions">The review was saved successfully</p>
            <Button variant="contained" onClick={handleGoHomePage}>
              Access Reviews
            </Button>
          </div>
        ) : (
          <div className="container-and-button">
            {activeStep !== 0 ? (
              <div className="container-next-and-back">
                <Button onClick={handleBack} className="back-button">
                  Back
                </Button>
              </div>
            ) : (
              <div className="container-next-and-back"></div>
            )}
            <div className="stepper-container">
              <Typography className="instructions">
                {getStepContent(activeStep)}
              </Typography>
            </div>
            <div className="container-next-and-back">
              <Button className="next-button" onClick={handleNext}>
                {activeStep === steps.length - 1 ? 'Create' : 'Next'}
              </Button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
