import React from 'react';
import TableReviews from '../../components/tableReviews/tableReviews';
import './ownerHome.css';
import AddButton from '../../components/addButton/addButton';
import GetApp from '@material-ui/icons/GetApp';
import PropTypes from 'prop-types';
import {makeStyles} from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import AccessRequestOwner from './accessRequestOwner/accessRequestOwner';
import Badge from '@material-ui/core/Badge';
import {
  faClipboardCheck,
  faClipboardList,
} from '@fortawesome/free-solid-svg-icons';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';

function TabPanel(props) {
  const {children, value, index, ...other} = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={1}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    'aria-controls': `vertical-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
    display: 'flex',
    height: 400,
  },
}));

function VerticalTabs() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root}>
      <Tabs
        orientation="vertical"
        variant="scrollable"
        value={value}
        onChange={handleChange}
        aria-label="Vertical tabs example"
        className={classes.tabs}
      >
        <Tab
          label="REVIEWS"
          icon={
            <Badge badgeContent={8} color="error">
              <FontAwesomeIcon
                icon={faClipboardList}
                className="sidebar-button-icon"
              />
            </Badge>
          }
          {...a11yProps(0)}
        />

        <Tab
          label="ACCESS REQUESTS"
          icon={
            <Badge badgeContent={4} color="error">
              <FontAwesomeIcon
                icon={faClipboardCheck}
                className="sidebar-button-icon"
              />
            </Badge>
          }
          {...a11yProps(1)}
        />
      </Tabs>
      <TabPanel value={value} index={0} className="tab-content">
        <div className="grid-container">
          <TableReviews />
        </div>
      </TabPanel>
      <TabPanel value={value} index={1} className="tab-content">
        <div className="grid-container">
          <AccessRequestOwner />
        </div>
      </TabPanel>
    </div>
  );
}

function OwnerHome() {
  return (
    <div>
      <div className="export-button">
        <AddButton
          title={'Export'}
          icon={<GetApp className="icon-button-add" />}
        />
      </div>
      <div className="access-audit-container">
        <VerticalTabs />
      </div>
    </div>
  );
}

export default OwnerHome;
