import './App.css';
import Home from './pages/home.js';
import AccessAudit from './pages/accessAudit/accessAudit.js';
import OwnersAndReviewers from './pages/owners-and-reviewers/ownersAndReviewers';
import AccessRequest from './pages/accessRequest/accessRequest';
import StrongpointReviews from './pages/strongpointReviews/strongpointReviews';
import Header from './components/header/header.js';
import NewReview from './pages/strongpointReviews/newReview/newReview';
import {Switch, Route, HashRouter} from 'react-router-dom';
import AddResource from './pages/owners-and-reviewers/addResource/addResource';
import OwnerHome from './pages/ownerHome/ownerHome';
import ReviewDetails from './pages/ownerHome/reviewDetails/reviewDetails';
import AccessRequestOwner from './pages/ownerHome/accessRequestOwner/accessRequestOwner';

function App() {
  return (
    <HashRouter>
      <Header />
      <Switch>
        <Route path="/" exact component={Home} />
        <Route path="/access-audit" component={AccessAudit} />
        <Route path="/owners-and-reviewers" component={OwnersAndReviewers} />
        <Route path="/strongpoint-reviews" component={StrongpointReviews} />
        <Route path="/access-request" component={AccessRequest} />
        <Route path="/new-review" component={NewReview} />
        <Route path="/add-resource" component={AddResource} />
        <Route path="/owner-home" component={OwnerHome} />
        <Route path="/review-details" component={ReviewDetails} />
        <Route path="/access-request-owner" component={AccessRequestOwner} />
      </Switch>
    </HashRouter>
  );
}

export default App;
